@import url(https://rsms.me/inter/inter.css);
.search {
  padding: 1rem;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px 0 rgba(0,0,0,0.08);
  border: 0;
  width: 100%;
}

.loading {
  position: relative;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  background-color: #9880ff;
  color: #9880ff;
  -webkit-animation: dotFlashing 1s infinite linear alternate;
          animation: dotFlashing 1s infinite linear alternate;
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
}

.loading::before, .loading::after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 0;
}

.loading::before {
  left: -15px;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  background-color: #9880ff;
  color: #9880ff;
  -webkit-animation: dotFlashing 1s infinite alternate;
          animation: dotFlashing 1s infinite alternate;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.loading::after {
  left: 15px;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  background-color: #9880ff;
  color: #9880ff;
  -webkit-animation: dotFlashing 1s infinite alternate;
          animation: dotFlashing 1s infinite alternate;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

@-webkit-keyframes dotFlashing {
  0% {
    background-color: #9880ff;
  }
  50%,
  100% {
    background-color: #ebe6ff;
  }
}

@keyframes dotFlashing {
  0% {
    background-color: #9880ff;
  }
  50%,
  100% {
    background-color: #ebe6ff;
  }
}

.item {
  flex: 0 1 25%;
  padding: 1rem;
}

.item__container {
  position: relative;
  cursor: pointer;
  height: 16rem;
  border-radius: 8px;
  overflow: hidden;
  border: 0.5rem solid #fff;
  -webkit-transition: box-shadow 0.125s ease-in-out, -webkit-transform 0.125s ease-in-out;
  transition: box-shadow 0.125s ease-in-out, -webkit-transform 0.125s ease-in-out;
  transition: transform 0.125s ease-in-out, box-shadow 0.125s ease-in-out;
  transition: transform 0.125s ease-in-out, box-shadow 0.125s ease-in-out, -webkit-transform 0.125s ease-in-out;
}

.item__container:hover {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  box-shadow: 0 2px 5px 0 rgba(16,0,16,0.24),
    0 4px 8px 0 rgba(0,0,0,0.16),
    0 4px 12px 0 rgba(16,0,128,0.16);
}

.item__image {
  object-fit: cover;
  height: 100%;
  width: 100%;
  top: 0px;
  left: 0;
  position: absolute;
  border-radius: 5px;
  text-align: center;
  background-color: #f7f7f7;
}

.item__pending {
  background-color: #f7f7f7;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: 5px;
  top: 0;
  left: 0;
  -webkit-transition: 0.125s opacity ease-in-out;
  transition: 0.125s opacity ease-in-out;
}

.item__pending--visible {
  opacity: 1;
}

.item__pending--hidden {
  opacity: 0;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -1rem;
}

.modal {
  position: fixed;
  background-color: rgba(0,0,0,0.32);
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal__content {
  flex: 0 1 640px;
  padding: 0.5rem;
  background-color: #fff;
  display: flex;
  align-items: flex-start;
  border-radius: 5px;
}

.modal__image {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

* { box-sizing: border-box; }
*
html { font-family: 'Inter', sans-serif; }

@supports (font-variation-settings: normal) {
  html { font-family: 'Inter var', sans-serif; }
}

body {
  background-color: rgba(240,240,250,1);
}

/* typography */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  color: #333;
  margin: 0;
}

.caption {
  font-size: 0.75rem;
  color: #555;
}

/* component styles */

.app {
  margin: 0 auto;
  width: 100%;
}

.app__header {
  display: flex;
  align-items: center;
  background-color: rgba(0,0,0,0.02);
  padding: 0.5rem 2rem;
}

.app__search {
  flex: 0 1 70%;
  padding: 0 1rem;
}

.app__title,
.app__stats {
  flex: 0 1 15%;
}

.app__title {
  font-size: 2rem;
  color: #5454ff;
}

.app__content {
  padding: 2rem;
}

.app__description {
  padding-bottom: 1rem
}

